@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  background: white;
  color: black;
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.05em;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search {
  width: 60%;
  max-width: 800px;
  margin: 1rem;
  text-align: right;
  position: relative;
}
.search-box {
  border: 4px solid transparent;
  border-radius: 2px;
  font-size: 1.5rem;
  width: 100%;
  padding: 1rem;
  transition: width 0.3s;
  border: 2px solid #c7caca;
}
.search-box:focus {
  width: 100%;
  outline: none;
  border: 2px solid #9b9c9c;
}
.search-btn {
  height: 50%;
  width: 2em;
  margin-top: -1.7rem;
  position: absolute;
  top: 75%;
  right: 0rem;
  opacity: 0.2;
  background-color: transparent;
  border: 0;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("data:image/png;base64,");
}
.search-btn:hover {
  outline: none;
  opacity: 0.4;
  cursor: pointer;
}
.search-btn:focus {
  outline: none;
  opacity: 0.6;
}

ul.options {
  position: absolute;
  left: 25%;
  background: white;
  list-style: none;
  width: 50%;
  max-width: 600px;
  transition: width 0.2s;
  margin: 4.9rem 0rem;
  border: 2px solid #9b9c9c;
}

ul.options li {
  display: block;
  color: black;
  margin: 5px auto;
  padding: 10px;
  font-size: 1.2rem;
  width: 100%;
  border-radius: 2px;
}
ul.options li:hover {
  font-weight: bold;
  color: #00b4cc;
  cursor: pointer;
  transition: 0.3s all;
}

ul.options li.option-active {
  font-size: 1.5rem;
  color: #00b4cc;
}
.no-options {
  color: white;
}

.recipe {
  width: 50%;
  max-width: 600px;
  z-index: -1;
}

.recipe em {
  color: grey;
}

